import React from "react";
import { FaFacebookF, FaGithub, FaGoogle } from "react-icons/fa";
import { Link, Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useSelector, useDispatch } from "react-redux";
import { user, uerror, loginUserAsync, loding } from "../feature/authslice";

const Login = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const dispatch = useDispatch();
  const usert = useSelector(user);
  const error = useSelector(uerror);
  const lod = useSelector(loding);

  return (
    <>
      {usert ? (
        <Navigate to="/" replace={true}></Navigate>
      ) : lod ? (
        <div
          className="w-full   flex justify-center z-50"
          style={{ minHeight: "83vh" }}
        >
          {" "}
          <span className="loading loading-spinner text-secondary w-14"></span>
        </div>
      ) : (
        <>
          <div className=" w-full  h-screen   flex items-center justify-center z-20   bg-gradient-to-r from-indigo-500 via-purple-4000 to-pink-400 relative ">
            <div className="modal-box  content-center">
              <div className="modal-action flex flex-col justify-center mt-0">
                <form
                  onSubmit={handleSubmit((data) => {
                    dispatch(
                      loginUserAsync({
                        email: data.email,
                        password: data.password,
                      })
                    );
                  })}
                  className="card-body"
                  method="dialog"
                >
                  <h3 className="font-bold text-2xl text-center ">
                    Please Login!
                  </h3>

                  {/* email */}
                  <div className="form-control">
                    <label className="label">
                      <span className="label-text text-base">
                        Email <span className="textred">*</span>
                      </span>
                    </label>
                    <input
                      type="text"
                      placeholder="email"
                      className="input input-bordered"
                      {...register("email", {
                        required: "email is required",
                        pattern: {
                          value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi,
                          message: "email not valid",
                        },
                      })}
                    />
                    {errors.email && (
                      <p className="textred">{errors.email.message}</p>
                    )}
                  </div>

                  {/* password */}
                  <div className="form-control">
                    <label className="label">
                      <span className="label-text text-base">
                        Password <span className="textred">*</span>
                      </span>
                    </label>
                    <input
                      type="password"
                      placeholder="password"
                      autoComplete="true"
                      className="input input-bordered"
                      {...register("password", {
                        required: "password is required",
                      })}
                    />
                    {errors.password && (
                      <p className="text-red-500">{errors.password.message}</p>
                    )}
                    {errors.password
                      ? ""
                      : error && <p className="text-red-500">{error}</p>}
                  </div>

                  {/* login btn */}
                  <div className="form-control mt-4">
                    <button type="submit" className="btn bg-green text-white h">
                      Login
                    </button>
                  </div>
                  <p className="text-center my-2">
                    Donot have an account?{" "}
                    <Link to="/signup" className="underline textred ml-1 ht">
                      Signup Now
                    </Link>
                  </p>

                  <Link to="/">
                    <button
                      htmlFor="my_modal_5"
                      className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"
                    >
                      âœ•
                    </button>
                  </Link>
                </form>
                <div className="text-center space-x-3 mb-5">
                  <button className="btn btn-circle hg">
                    <FaGoogle />
                  </button>
                  <button className="btn btn-circle hg">
                    <FaFacebookF />
                  </button>
                  <button className="btn btn-circle hg">
                    <FaGithub />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default Login;
